import { Produto } from './produto.model';
import { Injectable } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar'
import { HttpClient } from '@angular/common/http'
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ProdutoService {

  private baseUrl = "http://localhost:3001/produtos"

  constructor(
    private snackBar: MatSnackBar,
    private http: HttpClient
  ) { }

  public exibirMensagem(mensagem: string): void {
    this.snackBar.open(mensagem, 'X', {
      duration: 3000,
      horizontalPosition: 'right',
      verticalPosition: 'top'
    })
  }

  public adicionar(produto: Produto): Observable<Produto> {
    console.log('Produto inserido: ', produto)
    return this.http.post<Produto>(this.baseUrl, produto)
  }

  public consultar(): Observable<Produto[]> {
    return this.http.get<Produto[]>(this.baseUrl)
  }
}
